@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab&display=swap);
body {
  font-family: 'Zilla Slab', sans-serif !important;
  background: linear-gradient(90deg, rgba(33,133,63,1) 0%, rgba(1,118,148,1) 66%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, monospace;
}

  
.zoom:hover {
  transform: scale(1.2);
}

.zoom:active {
  transform: scale(1.1);
}

.zoom:after {
  transform: scale(1);
}

.center {
  display: flex !important;
  justify-content: center !important;
}

.button {
  cursor: pointer;
}

.btn-link {
  text-decoration: none !important
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.card {
  text-align: center;
  background: linear-gradient(135deg, rgba(1, 204, 177, 0.9) 0%, rgba(1, 38, 61, 0.85) 99%) !important;
  border-style: none !important;
}
.sign-out {
    display: flex;
    justify-content: flex-end;
    font-size: 1rem;
    color:black;
    padding: 1% 1%;
}

.signbtn:hover {
    transform: scale(1.1) !important;
}


.Tilt {
    background: linear-gradient(90deg, rgba(33,133,63,1) 0%, rgba(1,118,148,1) 66%);
    margin-bottom: 20px;
}

.Tilt-inner {
    padding-top: 7px;
    padding-left: 7px;
    width: 120px
}

.detect {
    text-align: center;
    font-size: 4rem;
    flex-wrap: wrap;
}

.btn-shadow {
    margin: 0 13% !important;
    padding: 1% 1% !important;
}

.input {
    width: 90% !important;
    margin-right: 1% !important;
}

.try {
    margin: 1% 0;
    font-size: 2rem;
}


.sign-title {
    font-size: 3rem !important;
    font-weight: bolder !important;
    padding-bottom: 3% !important;
    display: flex !important;
    justify-content: center !important;
}

.form-control {
    width: 50% !important;
    margin: auto !important;
}

.submit {
    margin: auto !important;
    width: auto !important;
    background: rgb(8, 189, 212, 0.7) !important;
    border-style: none !important;
    color: black !important;
    display: inline;
}

.submit:hover {
    background: rgba(32, 214, 238, 0.7) !important;
    color: white !important;
    transform: scale(1.2);
    font-weight: bold !important;
}

.submit:active {
    background: rgba(32, 214, 238, 0.7) !important;
    color: white !important;
    transform: scale(1.1);
    font-weight: normal !important;
}



#emailhelp {
    padding-left: 5px;
    padding-top: 5px;
    color: black !important;
    font-style: italic;
    font-weight: 300 !important;
    flex-wrap: wrap;

}

.register {
    padding: 2% 0 2% 0;
    text-decoration: none !important;
    color: black !important;
    margin: 0 50%
}

.register:hover {
    text-decoration: underline !important;
    transform: scale(1.1);
}
.register-title {
    font-size: 2.7rem !important;
    font-weight: bolder !important;
    display: flex !important;
    justify-content: center !important;
}


.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 2px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}

